import React from "react";
import './lists.css';

class Description extends React.Component {
    render() {
        return (
                <div>
                    <h1 className="titleMargin">
                        Lista znakw:
                    </h1>
                    <div className="countryList">
                        <ul>
                            <h2>Kraje: </h2>
                            <li> Anglia</li>
                            <li> Japonia</li>
                            <li> Kanada</li>
                            <li> Niemcy</li>
                            <li> Rosja</li>
                            <li> Polska</li>
                            <li> Chiny</li>
                            <li> Tajlandia</li>
                            <li> Francja</li>
                            <li> USA</li>
                        </ul>

                        <ul>
                            <h2>Owoce: </h2>
                            <li> arbuz</li>
                            <li> jabko</li>
                            <li> liczi</li>
                            <li> orzech woski</li>
                            <li> poziomka</li>
                            <li> urawina</li>
                            <li> mango</li>
                            <li> gruszka</li>
                            <li> banan</li>
                        </ul>

                        <ul>
                            <h2>Kolory: </h2>
                            <li> his - czarny</li>
                            <li> bis - biay</li>
                            <li> ls - zielony</li>
                            <li> hungs - ty</li>
                            <li> lns - niebieski</li>
                            <li> hngs - czerwony</li>
                            <li> zs - fioletowy</li>
                            <li> hus - szary</li>
                            <li> zngs - brzowy</li>
                            <li> gnlns - oliwkowy</li>
                            <li> jn s - zoty</li>
                            <li> yn s - srebrny</li>
                            <li> chngs - pomaraczowy</li>
                        </ul>
                    </div>
            </div>
        );
    }
}

export default Description;


