import React from "react";
import GameMechanic from "../components/GameMechanic";

class Fruits extends React.Component {
    constructor(props) {
        super(props),
            this.state = {
                qNumber: 0,
                score: 0
            }
    }

    shouldComponentUpdate = (nextProps, nextState) => {
        if(this.state.score == nextState.score) {
            return true
        }
    }

    nextQuestion = () => {
        this.setState({
            qNumber: this.state.qNumber + 1
        })
    }

    getPoint = () => {
        this.setState({
            score: this.state.score + 1
        })
    }

    renderQuestion = (states) => {
        let question = ''
        let answers = ["", "", "", "", "", "", "", "", "", ""]

        switch (states) {
            case 0:
                question = {
                    name: "jabko",
                    correct: ""
                }
                break;

            case 1:
                question = {
                    name: "mango",
                    correct: ""
                }
                break;

            case 2:
                question = {
                    name: "banan",
                    correct: ""
                }
                break;


            case 3:
                question = {
                    name: "grejpfrut",
                    correct: ""
                }
                break;

            case 4:
                question = {
                    name: "poziomka",
                    correct: ""
                }
                break;

            case 5:
                question = {
                    name: "arbuz",
                    correct: ""
                }
                break;
            case 6:
                question = {
                    name: "gruszka",
                    correct: ""
                }
                break;

            default:
                return <p style={{ textAlign: 'center', fontSize: '26px' }}>Twj wynik to {this.state.score}/7 poprawnych odpowiedzi</p>
        }

        return <GameMechanic {...this.props} {...question} answers={answers} nextQuestion={this.nextQuestion} getPoint={this.getPoint} />
    }



    render() {
        return this.renderQuestion(this.state.qNumber)
    }
}

export default Fruits;

