import React, { Component } from "react";
import GameMechanic from "../components/GameMechanic";

class Colors extends React.Component {
    constructor(props) {
        super(props),
            this.state = {
                qNumber: 0,
                score: 0
            }
    }

    shouldComponentUpdate = (nextProps, nextState) => {
        if(this.state.score == nextState.score) {
            return true
        }
    }

    nextQuestion = () => {
        this.setState ({
            qNumber: this.state.qNumber + 1
        })
    }

    getPoint = () => {
        this.setState ({
            score: this.state.score + 1
        })
    }

    renderQuestion = (states) => {
        let question = ''
        let answers = ["", "", "", "", "", "", "", "", "", "", ""]

        switch (states) {
            case 0:
                question = {
                    name: "biay",
                    correct: ""
                }
                break;

            case 1:
                question = {
                    name: "zielony",
                    correct: ""
                }
                break;

            case 2:
                question = {
                    name: "czerwony",
                    correct: ""
                }
                break;


            case 3:
                question = {
                    name: "ty",
                    correct: ""
                }
                break;

            case 4:
                question = {
                    name: "niebieski",
                    correct: ""
                }
                break;

            case 5:
                question = {
                    name: "czarny",
                    correct: ""
                }
                break;
            case 6:
                question = {
                    name: "brzowy",
                    correct: ""
                }
                break;

            default: 
                return <p style={{textAlign: 'center', fontSize: '26px'}}>Twj wynik to {this.state.score}/7 poprawnych odpowiedzi</p>
        }

        return <GameMechanic {...this.props} {...question} answers={answers} nextQuestion={this.nextQuestion} getPoint={this.getPoint} />
    }



    render() {
        return this.renderQuestion(this.state.qNumber)
    }
}

export default Colors;

//  his - czarny
//  bis - biay
//  ls - zielony
//  hungs - ty
//  lns - niebieski
//  hngs - czerwony
//  zs - fioletowy
//  hus - szary
//  zngs - brzowy
//  chngs - pomaraczowy





