import React from "react";
import './description.css';


class Description extends React.Component {

    render() {
        return (
            <div>
                <h1 className="descrp" style={{ textAlign: 'center', marginBottom: '20px' }}>
                    Proste zwroty chiskie:
                </h1>
                <ul className="form-signin">
                    <li> [N ho] - Dzie dobry (nieformalnie do jednej osoby)</li>
                    <li> [Nn ho] - Dzie dobry (formalnie do jednej osoby)</li>
                    <li> [Nmen ho] - Dzie dobry (nieformalnie do 2 lub wicej osb)</li>
                    <li> [Zhwi ho] - Dzie dobry (formalnie do 2 lub wicej osb)</li>
                    <li> [Zoshang ho] - Dzie dobry (rano).</li>
                    <li> [Wnshang ho] - Dobry wieczr!</li>
                    <li> [sh de] - tak</li>
                    <li> [b] - nie</li>
                    <li> [Xixie] - Dzikuj.</li>
                    <li> [B kq] /  [B xi.] - Prosz bardzo / Nie ma za co.</li>
                    <li> [Gng] - Prosz (proszc o co).</li>
                    <li> [Gi n] (dos. Daj ci.) - Prosz (podajc co).</li>
                    <li> [Zijin] - Do widzenia!</li>
                    <li> [Wnn] - Dobranoc!</li>
                    <li> [Bibai] - Cze! Pa!</li>
                </ul>
            </div>
        );
    }
}

export default Description;


