import React, { Component } from "react";
import Description from "./components/Description";
import ReturnButton from "./components/ReturnButton";

class Hard extends Component {
  render() {
    return (
        <div>
            <Description name='Owoce ' descrp='Problemy z nauk chiskich znakw dla owocw moe by nauka owocw mniej popularnych w Polsce, takich jak:'
                liOne=' karambola (yngto)' liTwo=' kumkwat (jn j)' liThree=' rambutan (hng mo dn)' liFour=' durian (li lin)'
                levelColor='blue' goTo='/fruits-hard' {...this.props} />
            <Description name='Pastwa '
                descrp='Mamy take Pastwa nie posiadajce w sobie znaku "GU"() oraz ktrych fonetyka nie jest w aden sposb podobna do ich anglojzycznych odpowiednikw: '
                liOne=' Islandia (bng do)' liTwo=' Szwajcaria (ru sh)' liThree=' Grecja (x l)' liFour=' Estonia (i sh n y)'
                levelColor='blue' goTo='/countries-hard' {...this.props} />
            <Description name='Kolory '
                descrp='Inne przykady znakw dla kolorw w jzyku chiskim s midzy innymi:'
                liOne=' Srebrny (yn s)' liTwo=' Zoty (jn s)' liThree=' oliwkowy (gn ln s)' liFour=' rowy (fn hng s)'
                levelColor='blue' goTo='/colors-hard' {...this.props} />
            <ReturnButton btn='btn btn-primary' />
        </div>
    );
}
}

export default Hard;


