import React, { Component } from "react";
import Description from "./components/Description";
import ReturnButton from "./components/ReturnButton";

class Easy extends Component {
    render() {
        return (
            <div>
                <Description name='Owoce ' descrp='Wikszo owocw w jzyku chiskim posiada w swoim znaku element oznaczajcy traw, przykady:'
                    liOne=' ananas (b lu)' liTwo=' jabko (png gu)' liThree=' liczi (l zh)' liFour=' mango (mng gu)'
                    levelColor='blue' goTo='/fruits' {...this.props} />
                <Description name='Pastwa '
                    descrp='W przypadku pastw, powtarzajcym si elementem jest znak "GU"(), przykady krajw:'
                    liOne=' Francja (f gu)' liTwo=' Niemcy (d gu)' liThree=' Anglia (yng gu)' liFour=' Stany Zjednoczone (mi gu)'
                    levelColor='blue' goTo='/countries' {...this.props} />
                <Description name='Kolory '
                    descrp='Kady kolor koczy si znakiem "s" , dotyczy to wszystkich podstawowych kolorw i nie mamy od tego wyjtkw'
                    liOne=' Czerwony (hng s)' liTwo=' Niebieski (ln s)' liThree=' Zielony (l s)' liFour=' ty (hung s)'
                    levelColor='blue' goTo='/colors' {...this.props} />
                <ReturnButton btn='btn btn-primary' />
            </div>
        );
    }
}

export default Easy;


