import React, { Component } from "react";
import Description from "./components/Description";
import ReturnButton from "./components/ReturnButton";

class Medium extends Component {
    render() {
        return (
            <div>
                <Description name='Owoce ' descrp='Istnieje take duo znakw dla owocw nie posiadajcych w sobie elementu trawy, takie jak:'
                    liOne=' arbuz (xgu)' liTwo=' gruszka (l)' liThree=' kokos (y)' liFour=' liwka (li sh)'
                    levelColor='blue' goTo='/fruits-med' {...this.props} />
                <Description name='Pastwa '
                    descrp='Mamy take kraje, w ktrych nie wystpuje znak "GU"(), etymologia tych znakw wynika z fonetycznego zapoyczenia tych sw z j.angielskiego'
                    liOne=' Polska (b ln)' liTwo=' Czechy (ji k)' liThree=' Luxemburg (l sn bo)' liFour=' Albania ( r b n y)'
                    levelColor='blue' goTo='/countries-med' {...this.props} />
                <Description name='Kolory '
                    descrp='Intensywno kolorw moemy okrela dodajc odpowiednio n() dla ciemniejszego koloru i ling() dla janiejszego.'
                    liOne=' ciemny czerwony (nhngs)' liTwo=' jasny niebieski (linglns)' liThree=' jasny zielony (lingls)' liFour=' ciemny ty (nhungs)'
                    levelColor='blue' goTo='/colors-med' {...this.props} />
                <ReturnButton btn='btn btn-primary' />
            </div>
        );
    }
}

export default Medium;


